'use strict';

/**
* Degenerate distribution logarithm of probability density function (logPDF).
*
* @module @stdlib/stats-base-dists-degenerate-logpdf
*
* @example
* import logpdf from '@stdlib/stats-base-dists-degenerate-logpdf' ;
*
* var y = logpdf( 2.0, 0.0 );
* // returns -Infinity
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-degenerate-logpdf' ;
*
* var logPDF = factory( 10.0 );
*
* var y = logPDF( 10.0 );
* // returns Infinity
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
